

*:focus{
    outline: none;
    
}
body{
    background: #f0f0f0;
}
a{
    color:#474787;
}
button{
    height: 30px;
    font-size: 16px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    padding: 0px 8px;
    gap: 5px;
}
select:hover{
    cursor: pointer;
}

.folder{
    display: inline;
    margin-left: 20px;
    text-decoration: underline;
}
.folder:nth-of-type(1){
    margin-left: 0;
}
.folder:hover{
    cursor: pointer;
}

/* split this */
.modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 100;
}
.modal_title{
    color: black;
}
.modal_container{
    padding: 10px;
    width: 90%;
    display: block;
    margin: 0 auto;
}
