
#rule_header{
    display: flex;
    align-items: center;
}
#add_rule{
    border: 1px solid #ccc;
    background: #eee;

}

#add_rule:hover{
    cursor: pointer;
    border: 1px solid #aaa
}

#rules h2{
    font-size: 20px;
    font-weight: 500;
}

.rule{
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 2px;
    padding: 5px;
    margin-top: 10px;
}

.rule_btns{
    display: flex;
    justify-content: space-between;
}
.run_rule{
    border: 1px solid #ccc;
    background: #eee;
}

.run_rule:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

.remove_rule{
    border: 1px solid #f5613d;
    background: #eee;
}

.remove_rule:hover{
    cursor: pointer;
    background: #f5613d;
    color: white;
}

.success{
    background: #447330;
    color: white;
}

.fail{
    background: #c23616;
}