#updateData{
    position:fixed;
    width: 100%;
    height: 50px;
    top: 55px;
    border-bottom: 1px solid #ccc;
    display:flex;
    align-items: center;
    z-index: 0;
}
#update_data_container{
    width: 90%;
    display:flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}

#update_data_container_left{
    display: flex;
    align-items: center;
    gap: 15px;
}
#user{
    display: flex;
    align-items: center;
    background: #eee;
    height: 25px;
    padding: 0px 8px;
    gap: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}
#user:hover{
    cursor: pointer;
    border: 1px solid #aaa
}
#user_profile{
    width: 10px;
    height: 10px;
    background:gold;
    padding: 2px;
    border-radius: 50%;
}
#add_data{   
    background: #eee;
    border: 1px solid #ccc;

}
#add_data:hover{
    cursor: pointer;
    border: 1px solid #aaa
}
