#add_node_modal{
    width: 300px;
    height: 200px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    border: 1px solid #ccc;
}

#add_node_txt{
    width: 250px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}
#add_node_txt:focus{
    outline: none;
}
#add_node_btn{
    margin-top: 20px;
    border: 1px solid #ccc;
    background: #eee;

}
#add_node_btn:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}
