#view_recent_query_modal{
    width: 600px;
    max-height: 600px;
    padding: 20px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
    overflow-y: auto;
}

.recent_query{
    display: flex;
    justify-content: space-between;
    background: #eee;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 3px;
    margin-top: 10px;
}
.recent_query p:nth-child(1){
    width: 60%;
}

.recent_query:hover{
    border: 1px solid #bbb;
    cursor: pointer;
}
