.node{
    width: 320px;
    border-radius: 3px;
    background: white;
    border: 1px solid #ccc;
}

.node_title{
    padding:5px;
    font-size: 20px;
    margin: 0;
    text-align: center;
}

.node_stats{
    width: 90%;
    display: flex;
    margin: 0 auto;
    justify-content: center;
}
.node_stats h1{
    width: 100px;
    font-size: 16px;
    margin-left: 5px;
}
.node_stats h1:nth-child(1){
    margin-left: 0;
}

.node_footer{
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  
}

.node_footer_up{
    background: #009432;
}

.node_footer_down{
    background: #EA2027;
}
.node_down{
    color: #EA2027;
}
.node_down:hover{
    border:1px solid #EA2027;
    cursor: pointer;
}

.node_footer h1{
    font-size: 16px;
    font-weight: 400;
}

#add_node{
    border: 1px solid #ccc;
    background: #eee;
}

#add_node:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}