.header{
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #ccc;
    background: #3d3d75;
    position: fixed;
    z-index: 1;
    color: white;
}
#header_container{
    height: 100%;
    min-width: 600px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}
#header_container_left{
    display: flex;
    align-items: center;
}
#logo{
    font-size: 25px;
    padding: 5px;
}
#logo:hover{
    cursor: pointer;
}
#menu{
    height: 30px;
    display: flex;
    align-items: center;
    margin-left: 40px;
    background: #eee;
    border: 1px solid #ccc;
    border-radius: 3px;
}
.menu_item{
    height: 30px;
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 0 5px;
    user-select: none; 
    color:black;
    border: 1px solid transparent;
}
.menu_item:hover{
    cursor: pointer;
    border: 1px solid #ccc;
    
}
.menu_item_selected{
    background: #ccc;
}

.menu_item:nth-child(1){
   border-top-left-radius: 3px;
   border-bottom-left-radius: 3px;
}
.menu_item:last-child{
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
 }

.menu_item img{
    width: 25px;
    height: 25px;
}
.menu_item h1{
    font-size: 16px;
    font-weight: 400;
}

#need_help{
    background: #3d3d75;
    border: 1px solid #4c4c91;
    color: white;
}
#need_help:hover{
    cursor: pointer;
    background:#4c4c91;
}