
.data{
    width: 30%;
    max-width: 300px;
    background: #e0e0e0;
    box-shadow: inset 0 0 0 1px #ccc;
}
.text{
   min-height: 100px;
   max-height: 150px;
}
.data_image{
    width: 70px;
    display: block;
    margin: 0 auto;
}
.data_type{
    text-align: center;
    margin-top: 0;
}
.modal_image{
    width: 100%;
    display: block;
    margin: 0 auto;
}
.data_key{
    background: #2c2c54;
    display: flex;
    align-items: center;
}
.data_key:hover{
    cursor: pointer;
}
.data_key:hover h1{
    color: #6ba2ff;
    text-decoration: underline;
}
.data_key h1{
    font-weight: 500;
    font-size: 16px;
    color: white;
    padding-left: 5px;
}

.data_value{
    font-weight: 400;
    font-size: 16px;
    padding-left: 5px;
}
.filetext{
    white-space: pre-line;
}