

#main{
    padding-top: 110px;
    width: 90%;
    display:block;
    margin: 0 auto;
}

#main_title_wrapper{
    height: 70px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    gap: 10px;
}
#main_title{
    font-size: 40px;
    
}
#num_docs{
    margin-left: 10px;
    padding-top: 5px;
}

#add_folder{
    border: 1px solid #ccc;
    background: #eee;
}

#add_folder:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

#data_container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 20px;
}
