#add_data_modal{
    width: 400px;
    height: 420px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
}
#add_data_modal_container{
    width: 90%;
    display: block;
    margin: 0 auto;
}

/* make a class */
#add_data_folder:nth-child(2){
    margin-top: -10px;
}

#add_data_folder{
    width: 314px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}
#add_data_key{
    width: 300px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    margin-top: 10px;
}
#add_data_key::placeholder{
    color: black;
}
#add_data_value{
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    margin-top: 10px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  resize: none;
}

#add_data_file{
    margin-top: 10px;
}

#add_data_btn{
    margin-top: 20px;
    border: 1px solid #ccc;
    background: #eee;
}
#add_data_btn:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}