#add_rule_modal{
    position: relative;
    width: 700px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    border: 1px solid #ccc;
}

#task_box_desc{
    display: flex;
    align-items: center;
    gap: 10px;
}

#task_box{
    display: flex;
    align-items: center;
    gap: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
}

#rule_type_desc{
    width: 95px;
}

#rule_input_desc{
    width: 310px;
}
.task{
    border: 1px solid #ccc;
    background: #eee;
    padding: 4px;
}
.rule_select{
    width: 100px;
    height: 30px;
}
#rule_input{
    width: 300px;
    height: 60px;
    resize: none;
    padding: 10px;
}

#add_task{
    border: 1px solid #ccc;
    background: #eee;
    color: black;
}

#add_task:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

#added_tasks{
    border-bottom: 1px solid #ccc;
}

#deploy{
    border: 1px solid #ccc;
    background: #eee;
    margin-top: 20px;
 
}

#deploy:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}